.btn {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 10px 0;
  color: $color-light-grey;
  background-color: $color-sky-blue;
  border: none;
  cursor: pointer;
  border-radius: 3px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  font-family: inherit;
}

